* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.accordion {
    margin-top: 6rem;
    font-size: 20px;
    width: 100%;
}

.holder {
    list-style: none;
    width: 70%;
    margin: 0 auto;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.question {
    background: #29b0ac;
    color: white;
    padding: 1rem;
    -webkit-box-shadow: -1px -1px 3px 0px #1a1a1a4f;
            box-shadow: -1px -1px 3px 0px #1a1a1a4f;
    cursor: pointer;
    font-weight: 400;
    position: relative;
}

.question.open,
.question:hover {
    color: black;
}

.question:after {
    position: absolute;
    right: 22px;
    top: 20px;
    font-size: 15px;
    font-size: 1.5rem;
    font-family: ico;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "+";
}

.question.open:after {
    content: "-";
}

.answer {
    background: #f9f9f9;
    overflow: hidden;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    /* transform: translateY(-11%);*/
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    opacity: 0.7;
    padding: 0 0.6rem;
    background: whitesmoke;
    font-weight: 200;
    letter-spacing: 1px;
}

.answer.open {
    opacity: 1;
    padding: 1rem;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}


